import React, { Component } from 'react';
import { connect } from 'react-redux';
import { flipCard, matchCards, resetGame } from '../actions';
import Card from './Card';

class PlayGround extends Component {
  componentDidMount() {
    this.start();
  }

  handleClick = (name, index) => {
    const { openedFrameworks, flipCard, matchCards } = this.props;

    if (openedFrameworks.length === 2) {
      setTimeout(matchCards, 750);
    } else {
      flipCard(name, index);

      if (openedFrameworks.length === 1) {
        setTimeout(matchCards, 750);
      }
    }
  };

  start() {
    let duplicatedFrameworks = this.props.frameworks.concat(this.props.frameworks);
    let randomizedFrameworks = this.shuffle(duplicatedFrameworks);
    let finalizedFrameworks = randomizedFrameworks.map((name) => ({
      name,
      close: true,
      complete: false,
    }));

    this.props.resetGame(finalizedFrameworks);
  }

  shuffle(array) {
    let currentIndex = array.length,
      temporaryValue,
      randomIndex;

    while (0 !== currentIndex) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }

  restartGame = () => {
    this.start();
  };

  render() {
    const { finalizedFrameworks, score } = this.props;

    return (
      <div className="playground">
        <div className="score">Puan: {score}</div>
        <div className="cards">
          {finalizedFrameworks.map((framework, index) => (
            <Card
              key={index}
              framework={framework.name}
              close={framework.close}
              complete={framework.complete}
              onClick={() => this.handleClick(framework.name, index)}
            />
          ))}
        </div>
        <button className="restart-button" onClick={this.restartGame}>
          Yeniden Oyna
        </button>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  finalizedFrameworks: state.finalizedFrameworks,
  openedFrameworks: state.openedFrameworks,
  score: state.score,
  frameworks: state.frameworks,
});

const mapDispatchToProps = {
  flipCard,
  matchCards,
  resetGame,
};

export default connect(mapStateToProps, mapDispatchToProps)(PlayGround);